.article {
  // Get this from a var
  max-width: 64rem;
  padding: 0 $spacing-m;
  margin: 0 auto;
  font-size: 22px;
  line-height: 1.9;
  background: $color-white;

  @media (min-width: $breakpoint-l) {
    padding: 3rem 10rem;
  }

  &__body {
    margin: $spacing-xxxl 0 0;
  }

  &__title {
    // Get this size from a var
    font-size: 83px;
    line-height: 1.3;
    margin-bottom: 0;
    -webkit-text-fill-color: $color-black; /* Will override color (regardless of order) */
    -webkit-text-stroke-color: $color-black;
    -webkit-text-stroke-width: 3px;

    transition: $pace-fast $ease-in-out -webkit-text-fill-color,
      $pace-fast $ease-in-out -webkit-text-stroke-width,
      $pace-fast $ease-in-out -webkit-text-stroke-color;

    &:hover {
      -webkit-text-fill-color: white; /* Will override color (regardless of order) */
      -webkit-text-stroke-color: $color-primary-lighter;
    }
  }
  &__date {
    display: block;
    width: 100%;
    text-align: right;
  }

  // HTML element styling
  @media (min-width: $breakpoint-s) {
    p:not(:first-of-type) {
      @supports (hyphens: auto) {
        text-align: justify;
        hyphens: auto;
      }
    }
  }
  p:first-of-type {
    font-size: 26px;
  }

  h2 {
    margin: $spacing-xxxxl 0 0;
  }

  img {
    display: block;
    max-width: 100%;
    margin: 0 auto;
  }
}
