body {
  overflow-x: hidden; }

pre code {
  display: block;
  padding: 1.7rem;
  overflow-x: auto;
  border: 5px solid #383838; }

/* Layout */
.page-item {
  margin: 8rem 0;
  padding: 0; }

.content-item {
  margin: 2rem 0;
  padding: 0; }

.text-item {
  margin: 0.7rem 0;
  padding: 0; }

.constrain--s {
  max-width: 800px;
  margin: 0 auto; }

.constrain {
  max-width: 1200px;
  margin: 0 auto; }

.constrain--l {
  max-width: 1800px;
  margin: 0 auto; }

.detail {
  background-image: linear-gradient(#fff, #f9f8f7, #fff); }

body {
  font-family: Helvetica, sans-serif;
  line-height: 1.5; }

h1,
.h1 {
  font-size: 46px;
  font-family: "IdealSans";
  font-weight: bold;
  font-style: italic; }

h2,
.h2 {
  font-size: 32px;
  font-family: "IdealSans";
  font-weight: bold;
  font-style: italic; }

h3,
.h3 {
  font-size: 26px;
  font-family: "IdealSans";
  font-weight: bold;
  font-style: italic; }

h4,
h5,
h6,
.h4,
.h5,
.h6 {
  font-size: 20px;
  font-family: "IdealSans";
  font-weight: bold;
  font-style: italic; }

.section-title {
  padding: 0 1rem; }

.article-prev {
  border-bottom: 5px solid #383838;
  position: relative;
  padding: 1rem; }
  .article-prev__title {
    font-size: 46px;
    font-family: "IdealSans";
    font-weight: bold;
    font-style: italic;
    margin: 0 0 0.7rem; }
    .article-prev__title:before {
      content: '';
      z-index: -1;
      position: absolute;
      bottom: 0;
      left: 0.5rem;
      width: 100%;
      height: 100%;
      transform: translate(0, 2rem) scale(0.1);
      transform-origin: top left;
      transition: 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95) transform;
      background-color: rgba(183, 225, 212, 0.6); }
  .article-prev__intro {
    margin: 0; }
  .article-prev__pub-date {
    display: block;
    width: 100%;
    text-align: right; }
  .article-prev__reading-time {
    position: absolute;
    top: 0;
    right: 1rem; }
  .article-prev__link {
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .article-prev:hover .article-prev__title, .article-prev:focus .article-prev__title {
    cursor: pointer; }
    .article-prev:hover .article-prev__title:before, .article-prev:focus .article-prev__title:before {
      transform: translate(-0.1rem, -0.3rem) scale(1.05) rotate(-2deg); }

.article {
  max-width: 64rem;
  padding: 0 1rem;
  margin: 0 auto;
  font-size: 22px;
  line-height: 1.9;
  background: #fff; }
  @media (min-width: 1024px) {
    .article {
      padding: 3rem 10rem; } }
  .article__body {
    margin: 2.5rem 0 0; }
  .article__title {
    font-size: 83px;
    line-height: 1.3;
    margin-bottom: 0;
    -webkit-text-fill-color: #383838;
    /* Will override color (regardless of order) */
    -webkit-text-stroke-color: #383838;
    -webkit-text-stroke-width: 3px;
    transition: 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95) -webkit-text-fill-color, 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95) -webkit-text-stroke-width, 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95) -webkit-text-stroke-color; }
    .article__title:hover {
      -webkit-text-fill-color: white;
      /* Will override color (regardless of order) */
      -webkit-text-stroke-color: rgba(183, 225, 212, 0.6); }
  .article__date {
    display: block;
    width: 100%;
    text-align: right; }
  @media (min-width: 480px) {
    @supports (hyphens: auto) {
      .article p:not(:first-of-type) {
        text-align: justify;
        hyphens: auto; } } }
  .article p:first-of-type {
    font-size: 26px; }
  .article h2 {
    margin: 7rem 0 0; }
  .article img {
    display: block;
    max-width: 100%;
    margin: 0 auto; }

.article-list {
  margin: 0;
  padding: 0;
  list-style-type: none; }
  .article-list__item {
    padding: 0; }

.footer {
  display: flex;
  justify-content: center;
  align-self: center; }
  .footer__link {
    color: #383838;
    transition: 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95) color; }
    .footer__link:hover, .footer__link:focus {
      color: #a5d9c9; }

.nav__list {
  margin: 0;
  padding: 0 1rem;
  display: flex;
  justify-content: center; }

.nav__item {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.nav__link {
  position: relative;
  display: block;
  padding: 1rem;
  color: inherit;
  text-decoration: none;
  font-family: "IdealSans";
  font-style: italic;
  font-size: 26px; }
  .nav__link:before {
    content: '';
    z-index: -1;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    transform: translate(80%, 2rem) scale(0.1);
    transform-origin: top left;
    transition: 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95) transform;
    background-color: rgba(241, 130, 116, 0.6); }
  .nav__link:hover, .nav__link:focus {
    cursor: pointer; }
    .nav__link:hover:before, .nav__link:focus:before {
      transform: translate(-0.1rem, -0.3rem) scale(1.05) rotate(-2deg); }
  .nav__link--active:before {
    transform: translate(-0.1rem, -0.3rem) scale(1.05) rotate(-2deg); }
